.checkoutProduct {
  display: flex;
  margin: 20px 0px;
}

.checkoutProduct__infor {
  padding-left: 20px;
}

.checkoutProduct__infor > button {
  background-color: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.checkoutProduct__image {
  object-fit: contain;
  width: 180px;
  /* height: 180px; */
}

.checkoutProduct__rating {
  display: flex;
}

.checkoutProduct__title {
  font-size: 17px;
  font-weight: 800;
}
