.home {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  width: 80%;
}

.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
  width: 100%;
}

.home__image_container {
  position: relative;
  display: flex;
  overflow: hidden;
  /* max-width: 100vw; */
}

.home__image {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__image:not(:last-child) {
  margin-right: 12.5%;
}

/* Radio button */

.home__image_container > input {
  display: none;
  color: black;
}

.first {
  transition: all 2s;
  -webkit-transition: all 2s;
  -moz-transition: all 2s;
  -ms-transition: all 2s;
  -o-transition: all 2s;
}

#radio1:checked ~ .first {
  margin-left: -0%;
}

#radio2:checked ~ .first {
  margin-left: -112.5%;
}

#radio3:checked ~ .first {
  margin-left: -225%;
}

#radio4:checked ~ .first {
  margin-left: -337.5%;
}

/* Navigation Button */
.navigation__btn {
  position: absolute;
  left: 0;
  width: 100%;
  bottom: 10%;
  display: flex;
  justify-content: center;
}

.manual__btn {
  border: 2px solid #f0c14b;
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
  transition: 1s;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -ms-transition: 1s;
  -o-transition: 1s;
}

.manual__btn:not(:last-child) {
  margin-right: 40px;
}

.manual__btn:hover {
  background-color: #f0c14b;
}
